body{
  background-color: #262626;
}
.App{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
}
.keyPressed{
  color: #f2f2f2;
}
.drum-pads{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 2px;
  row-gap: 2px;
}
.drum-pad{
  border: 1px solid #262626;
  padding: 30px;
  background-color: #f2f2f2;
  color: #262626;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s all;
}
.drum-pad:hover{
  cursor: pointer;
  color: #f2f2f2;
  background-color: rgb(221, 105, 105);
}
